import React, { Component } from "react";
import MediaCard from './FounderCard';
import styles from './PageDivider.css';



const Brenda = () => {
   return(
      <div className={styles.container} >
        <h1>Brenda</h1>
        <p>
          The Haitian American Medical Association (HAMA) has been a passion
          project of mine since 2008. Over the course of several years I’ve
          accumulated a number of ideas, potential projects, and goals for 
          this organization. Year after year I would make the decision to
          launch this non-profit, but there was always something holding me back, 
          specifically fear. As I embarked on this unfamiliar path, I was extremely
          hesitant about moving outside of my comfort zone. Despite my personal obstacles, 
          in October of 2016 I decided to step out on faith and live in my purpose.
        </p>
         <p>
          I always knew my career would be in the medical field. In a bold attempt to
          run from my calling, I decided to attend Johnson and Wales University in Providence,
          RI with the hopes of becoming an international business woman; that was short lived.
          After one year of detouring, I transferred to Regis College in Weston, MA to
          pursue a nursing degree. I graduated in May 2011 with my Bachelors of Science
          in Nursing. I then returned to Regis College to further my education obtaining
          a Master’s of Science degree (Family Nurse Practitioner) in 2015.
        </p>
        <p>
          Long before HAMA had its official name, I always envisioned creating an organization 
          that would improve the health and wellbeing of our Haitian communities. On several 
          occasions, I have been asked – why an organization for Haitians only? At first, I 
          believed it was due to my multiple trips to Haiti, and growing up immersed in my culture 
          that influenced my decision. However, what I realized is that my “why” ran deeper. In these 
          past six years of being a Nurse/Nurse Practitioner, my “why” has been deeply cultivated by 
          the Haitian patients I’ve had the opportunity to care for. As I witnessed how the lack of 
          basic health education can cause a lifetime of complications – that could have otherwise been 
          detected earlier or prevented – HAMA was propelled into existence. I believe, cultural sensitivity 
          is the fundamental building block to providing strategic health education.
        </p>
         <p>
          For this reason, it is my goal for HAMA to be the premier platform for providing culturally-specific 
          health information to Haitian American communities to improve health literacy and achieve optimal 
          health outcomes. Although Boston is HAMA’s geographical starting point, I envision this organization 
          transcending borders by collaborating with other organizations and reaching Haitian communities in 
          other states and countries. As I always say “humble beginnings make for unforgettable memories”.
        </p>
        <span>Thank you in advance for your support to our organization!</span>
        <MediaCard />
      </div>
   	)
}
 
class About extends Component {
  render() {
    return (
      <div>
        <Brenda />
      </div>
    );
  }
}
 
export default About;